@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: #23252C;
  font-family: "Inter", sans-serif;
}

.container {
  border-radius: 10px;
  overflow: hidden;
  background: #1A1B21;
  width: 317px;
  max-height: 1000px;
  /* i just wanted to center this thing on the page */
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.info--pfp {
  width: 317px;
  height: 317px;
  object-fit: cover;
  object-position: 0 90%;
}

.info--name {
  color: hsla(0, 0%, 100%, 1);
  padding-top: 21px;
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
}

.info--title {
  color: hsla(25, 79%, 78%, 1);
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 15px;
}

.info--website {
  color: #F5F5F5;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 150%;
  padding-top: 9px;
  padding-bottom: 15px;
}

.info--link {
  text-decoration: none;
}

.info--buttons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-bottom: 30px;
  width: 100%;
}

.info--buttons__email, .info--buttons__linkedin {
  padding: 9px 11px;
  border-radius: 10px;
  width: 115px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.info--buttons__email {
  background: hsla(0, 0%, 100%, 1);
  margin-left: 35px;
}

.info--buttons__email--icon {
  margin-right: 8px;
}

.info--buttons__email--link {
  text-decoration: none;
  color: black;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
}

.info--buttons__linkedin {
  background-color: hsla(212, 72%, 60%, 1);
  margin-right: 35px;
}

.info--buttons__linkedin--icon {
  margin-right: 8px;
}

.info--buttons__linkedin--link {
  text-decoration: none;
  color: hsla(0, 0%, 100%, 1);
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
}

.about, .interests {
  padding: 0 34px;
}

.interests {
  padding-top: 20px;
  padding-bottom: 30px;
}

.about--title, .interests--title {
  color: hsla(0, 0%, 96%, 1);
  font-weight: 700;
  font-size: 18px;
  line-height: 150%;
}

.about--text, .interests--text {
  color: hsla(0, 0%, 86%, 1);
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
}

.footer {
  display: flex;
  padding: 19px 73px;
  justify-content: space-between;
  align-items: center;
  background: #161619;
  height: 64px;
}